    import {
        mapState
    } from 'vuex';
    import uniNumberBox from '@/components/uni-number-box.vue'
    export default {
        components: {
            uniNumberBox
        },
        data() {
            return {
                total: 0, //总价格
                allChecked: false, //全选状态  true|false
                empty: false, //空白页现实  true|false
                cartList: [],
            };
        },
        onShow(){
            this.loadData();

        },
        watch:{

            //显示空白页
            cartList(e){
                let empty = e.length === 0 ? true: false;
                if(this.empty !== empty){
                    this.empty = empty;
                }
            }
        },
        computed:{
            ...mapState(['hasLogin','userInfo'])
        },
        methods: {
            //请求数据
             loadData(){
                var _this = this
                console.log("carList!!!!!!")
				var params = this.userInfo.id
                this.$http.post("cart/checkMyCart?userId="+ params).then((res) => {
                    console.log("success")
                    console.log(res)
                    if (res.data.status == "success") {
						var list = res.data.data
                        let cartList = list.map(item=>{
                            item.checked = true;
                            return item;
                        });
                        _this.cartList = cartList;
                        _this.calcTotal();  //计算总价
                    }else {
                        this.$api.msg(res.data.message);
                    }
                }).catch(error => {
                    console.log(error)
                }).finally(() => {

                })
                //let list = await this.$api.json('cartList');
                //cartList添加属性

            },
            //监听image加载完成
            onImageLoad(key, index) {
                this.$set(this[key][index], 'loaded', 'loaded');
            },
            //监听image加载失败
            onImageError(key, index) {
                 console.log("ImageErroe")
                this[key][index].image = '/static/errorImage.jpg';
            },
            navToLogin(){
                uni.navigateTo({
                    url: '/pages/public/login'
                })
            },
            //选中状态处理
            check(type, index){
                if(type === 'item'){
                    this.cartList[index].checked = !this.cartList[index].checked;
                }else{
                    const checked = !this.allChecked
                    const list = this.cartList;
                    list.forEach(item=>{
                        item.checked = checked;
                    })
                    this.allChecked = checked;
                }
                this.calcTotal(type);
            },
            //数量
            numberChange(data){
                console.log(data.index)
                console.log(data.number)
				if(data.number == 1){
                    this.$api.msg("不能在减少了呀！")
				}

                this.cartList[data.index].num = data.number;
                this.calcTotal();
            },
            //删除
            deleteCartItem(id){
                console.log(id)
				var _this = this
                this.$http.post("cart/deleteSelectFruit?id="+ id).then((res) => {
                    console.log("success")
                    console.log(res)
                    if (res.data.status == "success") {
						this.loadData();
                    }else {
                        this.$api.msg(res.data.message);
                    }
                }).catch(error => {
                    console.log(error)
                    return
                }).finally(() => {

                })
                // this.calcTotal();
                // uni.hideLoading();
            },
            //清空
            clearCart(){
                 var id = this.userInfo.id
                uni.showModal({
                    content: '清空购物车？',
                    success: (e)=>{
                        this.$http.post("cart/deleteAllFruit?id="+ id).then((res) => {
                            console.log("success")
                            console.log(res)
                            if (res.data.status == "success") {
                                this.$api.msg(res.data.message)
                                this.cartList = [];
                            }else {
                                this.$api.msg(res.data.message);
                            }
                        }).catch(error => {
                            console.log(error)
                            return
                        }).finally(() => {

                        })
                    }
                })
            },
            //计算总价
            calcTotal(){
                let list = this.cartList;
                if(list.length === 0){
                    this.empty = true;
                    return;
                }
                let total = 0;
                let checked = true;
                list.forEach(item=>{
                    if(item.checked === true){
                        total += item.price * item.num;
                    }else if(checked === true){
                        checked = false;
                    }
                })
                this.allChecked = checked;
                this.total = Number(total.toFixed(2));
            },
            //创建订单
            createOrder(){
                let list = this.cartList;
                let goodsData = [];
                list.forEach(item=>{
                    if(item.checked){
                        goodsData.push({
							item
                        })
                    }
                })
				console.log(goodsData)
                uni.navigateTo({
                    url: `/pages/order/createOrder?data=${JSON.stringify({
                        goodsData: goodsData,
						total:this.total
                    })}`
                })
            }
        }
    }
